import http from "@/axios"
import store from "@/store"
import Vue from "vue"

export default {
    name:"commit_offline_activity",
    methods:{
        commitActivity(){
            var self = this;
            http.submitActivity(this.$route.query.id,{
                activityForm :0,
                //TODO 待做
                deliverImage1 :"https://desk-fd.zol-img.com.cn/t_s1024x768c5/g5/M00/0F/08/ChMkJlauzFOIEC4LAAhwlr8IRvMAAH88wNxMdcACHCu543.jpg" ,
                questionStatus :"2" ,
            },function (response) {
                Vue.$vux.toast.show({text:"提交成功"});
                self.$router.go(-1);
                store.commit('UPDATE_ACTIVITY_STATUS', {activityStatus:1})
            });
        }
    }
}



